	module.exports = {"data":{"markdownRemark":{"html":"<p>   This portion of southern mirkwood has a developed an interesting flora and\nfauna influenced mostly by the presence of the wetlands and marshs that\nsurround the River Morduin. The soils are rich and fertile and as a result the\nvegetation is lush and splendid. Huge carnivorous vines commonly hang from\ntrees thriving off the humid atmosphere. The subterranean fauna of the forest\nis as rich and as varied as the surface fauna, taking advantage of natural\nchalk caves and caverns. Trapdoor spiders and giant termites are abundant and\ntravellers should be careful to avoid their habitats. The giant termites are\nof particular interest to the keen ecologist as their size, abundance and\nsocial structure seems to be uniquely tied to the strange micro-climate  that\nexists around their mounds. Few trails pass through this area and their are no\nsigns of any civilisation. It is too far north for the orcs of Dol-Guldur and\nthe woodsmen prefer to keep to the  safer areas surrounding Osterin and\nTawarost.</p>\n<p>Aeglos, Sage and Scribe of Loth Lanath.</p>","frontmatter":{"title":"The Giant Termites of South Mirkwood"}}},"pathContext":{"path":"/books/gianttermitesofsmirkwood"}}

